@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  padding: 0;
  margin: 0;
  font-family: 'Nunito', sans-serif;
}

.carousel-caption {
  position: absolute;
  right: 20%;
  bottom: 20px;
  top: 20%;
  left: 15%;
  z-index: 10;
  width: 30%;
  padding-top: 20px;
  padding-bottom: 20px;
  color: #fff;
  text-align: center;
 
  
}

@media (max-width: 991px)
{
  .carousel-caption 
   {
    position: absolute;
    right: 0;
    bottom: 20px;
    top: 0;
    left: 30px;
    z-index: 10;
    width: 80%;
    padding-top: 20px;
    padding-bottom: 20px;
    color: #fff;
    text-align: center;
   }
}

.carousel-indicators li {
    border-radius: 50%;
    width: 10px;
    height: 10px;

}

.carousel-control-prev, .carousel-control-next{
  display: none;
}

.slick-slider.slick-initialized{
  width: 100%;
}